/* *,
*:before,
*:after {
  box-sizing: border-box;
} */
*::selection 
{
    background-color:transparent;
} 

*::-moz-selection
{
    background-color:transparent;
}
*
{        
    -webkit-user-select: none;
    /*IE10*/
    -ms-user-select: none;
    user-select: none;

    /*You just need this if you are only concerned with android and not desktop browsers.*/
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
*{
